<template>
  <div class="test container">
      <h1 class="text-primary">{{title}}</h1>
      <form v-on:submit="getDevices" class="form form-inline">
        <input class="form-control" id="message" type="text" placeholder="Message to the module">
        <!-- <input class="form-control" id="characteristic" type="text" placeholder="Characteristic"> -->
        <br>
        <br>
        <input class="btn btn-success" type="submit" value="Get and Write">
      </form>
      <br>
      <div class="panel panel-default">
        <div class="panel-heading">
          Output
        </div>
        <div id="output" class="panel-body">
          <div id="content"></div>
          <div id="status" class="text-danger"></div>
          <pre id="log" style="text-align:left"></pre>
        </div>
      </div>
  </div>
</template>

<script>
    export default {
        name: 'test',
        data() {
            return {
                title: 'ArkPos2 BLE Config',
                user: {
                  firstName: 'Nick',
                  lastName: 'Tsilivis'
                }
            }
        },
        mounted: function () {
          if (/Chrome\/(\d+\.\d+.\d+.\d+)/.test(navigator.userAgent)){
            if (45 > parseInt(RegExp.$1)) {
              this.setStatus('Warning! Keep in mind this sample has been tested with Chrome ' + 45 + '.');
            }
          }

          if (!navigator.bluetooth) {
            this.setStatus('Web Bluetooth API is not available.\n');
          }
        },
        methods: {
          getDevices: function(e) {
            e.preventDefault();

            this.clearLog();

            // if (navigator.bluetooth) {
              let filters = [];

              // let serviceUuid = document.querySelector('#service').value;
              let serviceUuid = "0x0FF"
              if (serviceUuid.startsWith('0x')) {
                serviceUuid = parseInt(serviceUuid);
              }
              this.log('Service UUID: ' + serviceUuid);

              let characteristicUuid = "0xFF01";
              // let characteristicUuid = document.querySelector('#characteristic').value;
              if (characteristicUuid.startsWith('0x')) {
                characteristicUuid = parseInt(characteristicUuid);
              }

              let message = document.querySelector('#message').value;

              this.log('Characteristic UUID: ' + characteristicUuid);

              this.log('Requesting Bluetooth Device...');

              navigator.bluetooth.requestDevice({
                filters: [{
                  services: [
                    serviceUuid]
                  }]
                })
              .then(device => {
                this.log('-> Name:      ' + device.name);
                this.log('-> ID:        ' + device.id);

                return device.gatt.connect()
              })
              .then(server =>{
                this.log('Getting Service');
                return server.getPrimaryService(serviceUuid);
              })
              .then(service => {
                this.log('Getting characteristic');
                if (characteristicUuid) {
                  return service.getCharacteristic(characteristicUuid);
                }
              })
              .then(characteristic => {
                let encoder = new TextEncoder('utf-8');
                return characteristic.writeValue(encoder.encode(message));
              })
              .then(_ => {
                this.log("Value written");
              })
              // .then(characteristics => {
              //   console.log(characteristics);
              //   this.log('> Characteristics: ' + characteristics.map(c => c.uuid).join('\n' + ' '.repeat(19)));
              // })
              .catch(error => {
                this.log('Argh! ' + error);
              });
            // }
            // else {
            //   alert('No Bluetooth');
            // }

          },
          log: function () {
            var line = Array.prototype.slice.call(arguments).map(function(argument) {
              return typeof argument === 'string' ? argument : JSON.stringify(argument);
            }).join(' ');

            document.querySelector('#log').textContent += line + '\n';
          },
          clearLog: function() {
            document.querySelector('#log').textContent = '';
          },
          setStatus: function(status) {
              document.querySelector('#status').textContent = status;
          },
          setContent: function () {
            var content = document.querySelector('#content');
            while (content.hasChildNodes()) {
              content.removeChild(content.lastChild);
            }
            content.appendChild(newContent);
          }
        }
    }
</script>

<style scoped>

</style>



